<template>
  <div class="progress-bar" ref="progressWrapper" @click="progressCLick">
    <div class="bar-inner">
      <div class="progress" ref="progress"></div>
      <div class="progress-btn-wrapper" ref="btn"
           @touchstart.prevent="progressTouchStart"
           @touchmove.prevent="progressTouchMove"
           @touchend="progressTouchEnd"
      >
        <div class="progress-btn" ></div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import {prefixStyle} from 'common/js/dom'
  const transform = prefixStyle('transform')
  const BTN_WIDTH = 16

  export default{
      props:{
       percent:{
           type:Number,
           default:0
       }
      },
    created(){
          this.touch={ }
    },
    methods:{
        /*滑动进度条*/
          progressTouchStart(e){
            this.touch.initialed=true
            this.touch.startX=e.touches[0].pageX
            this.touch.left=this.$refs.progress.clientWidth
          },
          progressTouchMove(e){
            if(!this.touch.initialed)
            {
              return
            }
            const detaX=e.touches[0].pageX-this.touch.startX
            const offsetWidth=Math.min(Math.max(this.touch.left+detaX,0),this.$refs.progressWrapper.clientWidth-BTN_WIDTH)
            this._offset(offsetWidth)
            this._triggerPercent()
          },
          progressTouchEnd(e){
             this.touch.initialed=false
          },
        progressCLick(e){
              /*这里当点击 progressBtn时候 获取e.offsetX不对*/
              let clientX=e.clientX
              const rect=this.$refs.progress.getBoundingClientRect()
              const offsetWidth=clientX-rect.left
             this._offset(offsetWidth)
             this._triggerPercent()
        },
        /*更改进度条的偏移量以及btn的位置*/
        _offset(offsetWidth){
          this.$refs.progress.style.width=`${offsetWidth}px`
          this.$refs.btn.style[transform]=`translate3d(${offsetWidth}px,0,0)`
        },
      _triggerPercent(){
        let percent=  this.$refs.progress.clientWidth / (this.$refs.progressWrapper.clientWidth-BTN_WIDTH)
        this.$emit('BarTouch',percent)/*向父组件派发事件*/
      }
    },
   watch:{
          percent(newPercent){
              if(newPercent>=0 && !this.touch.initialed)
              {
                const barWidth=this.$refs.progressWrapper.clientWidth-BTN_WIDTH
                const offsetWidth=barWidth*newPercent
                this._offset(offsetWidth)
              }
          }
   }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .progress-bar
    height: 30px
    .bar-inner
      position: relative
      top: 13px
      height: 4px
      background: rgba(0, 0, 0, 0.3)
      .progress
        position: absolute
        height: 100%
        background: $color-theme
      .progress-btn-wrapper
        position: absolute
        left: -8px
        top: -13px
        width: 30px
        height: 30px
        .progress-btn
          position: relative
          top: 7px
          left: 7px
          box-sizing: border-box
          width: 16px
          height: 16px
          border: 3px solid $color-text
          border-radius: 50%
          background: $color-theme
</style>



