import { mapGetters ,mapMutations,mapActions} from 'vuex'
import { playMode } from 'common/js/config'
import {shuffle} from 'common/js/util'

export const playListMixin = {
  computed:{
    ...mapGetters([
      'playList'
    ])
  },
   mounted() {
     this.handlePlayList(this.playList)
   },
  activated() {
    this.handlePlayList(this.playList)
  },
 watch:{
    playList(newVal){
      this.handlePlayList(newVal)
    }
 },
  methods:{
    handlePlayList(){
      throw new Error("component must implement handlePlayList method")
    }
  }
}


export  const playerMixin = {
  computed:{
    playMode(){
      return this.mode === playMode.sequence ? 'icon-sequence' : this.mode === playMode.random ? 'icon-random' : 'icon-loop'
    },
    ...mapGetters(['sequenceList','currentSong','playList','playing','mode', 'favoriteList']),
  },
 methods:{
   modeChange(){
     const mode = (this.mode+1) % 3
     this.setPlayMode(mode)
     let list=null
     if(this.mode === playMode.random)
     {
       list=shuffle(this.sequenceList)
     }
     else
     {
       list = this.sequenceList
     }
     this.resetCurrentIndex(list)
     this.setPlayList(list)
   },
   resetCurrentIndex(list){
     let index = list.findIndex( (item) => {
       return item.id === this.currentSong.id
     })
     this.setCurrentIndex(index)
   },
   toggleFavorite(song) {
     if (this.isFavorite(song)) {
       this.deleteFavoriteList(song)
     } else {
       this.saveFavoriteList(song)
     }
   },
   getFavoriteIcon(song) {
     if (this.isFavorite(song)) {
       return 'icon-favorite'
     }
     return 'icon-not-favorite'
   },
   isFavorite(song) {
     const index = this.favoriteList.findIndex((item) => {
       return item.id === song.id
     })
     return index > -1
   },
   ...mapMutations({
     setPlayingState:'SET_PLAYING_STATE',
     setCurrentIndex:'SET_CURRENT_INDEX',
     setPlayMode:'SET_PLAY_MODE',
     setPlayList:'SET_PLAYLIST'
   }),
   ...mapActions([
     'saveFavoriteList',
     'deleteFavoriteList'
   ])
 }
}

export const searchMixin ={
  data(){
    return {
      query:'',
    }
  },
  computed:{
    ...mapGetters(['searchHistory']),
  },
 methods:{
   blurInput(){
     /*调用子组件的失焦函数，再输入之后，滚动时利用失焦将键盘收起*/
     this.$refs.searchBox.blur()
   },
   saveSearch(){
     this.saveSearchHistory(this.query)
   },
   search(query){
     this.query = query
   },
   addQuery(query){
     this.$refs.searchBox.addQuery(query)
   },
   ...mapActions(['saveSearchHistory','deleteSearchHistory'])
 }
}



