<template>
  <div class="player" v-show="playList.length>0">
    <transition name="normal"
                @enter = "enter"
                @after-enter="afterEnter"
                @leave="leave"
                @after-leave="afterLeave"
    >
      <div class="normal-player" v-show="fullScreen">
        <div class="background">
          <img width="100%" height="100%">
        </div>
        <div class="top">
          <div class="back" @click="back">
            <i class="icon-back"></i>
          </div>
          <h1 class="title" v-html="currentSong.name"></h1>
          <h2 class="subtitle" v-html="currentSong.singer"></h2>
        </div>
        <div class="middle"
             @touchstart.prevent="middleTouchStart"
             @touchmove.prevent="middleTouchMove"
             @touchend.prevent="middleTouchEnd"
        >
          <div class="middle-l" ref="middleL">
            <div class="cd-wrapper" ref="cdWrapper">
              <div class="cd" :class="CDClass">
                <img class="image" :src="currentSong.image">
              </div>
              <div class="playing-lyric-wrapper">
                <div class="playing-lyric">{{playingLyric}}</div>
              </div>
            </div>
          </div>
          <scroll class="middle-r" ref="lyricList" :data="currentLyric && currentLyric.lines ">
            <div class="lyric-wrapper">
              <div v-if="currentLyric">
                <p ref="lyricLine" class="text" v-for="(line,index) in currentLyric.lines" :class="{'current':currentLineNum === index}">{{line.txt}}</p>
              </div>
            </div>
          </scroll>
        </div>
        <div class="bottom">
          <div class="dot-wrapper">
            <span class="dot" :class="{'active':currentShow === 'cd'}"></span>
            <span class="dot" :class="{'active':currentShow === 'lyric'}"></span>
          </div>
          <div class="progress-wrapper">
            <span class="time time-l">{{format(currentTime)}}</span>
            <div class="progress-bar-wrapper">
              <progress-bar :percent="percent" @BarTouch="barTouch"></progress-bar>
            </div>
            <span class="time time-r">{{format(currentSong.duration)}}</span>
          </div>
          <div class="operators">
            <div class="icon i-left" :class="disableCls"  @click="modeChange">
              <i :class="playMode"></i>
            </div>
            <div class="icon i-left" :class="disableCls">
              <i class="icon-prev" @click="playPreSong" ></i>
            </div>
            <div class="icon i-center" :class="disableCls">
              <i :class="playIcon" @click="togglePlay" ></i>
            </div>
            <div class="icon i-right" :class="disableCls">
              <i class="icon-next" @click="playNextSong" ></i>
            </div>
            <div class="icon i-right">
              <i @click="toggleFavorite(currentSong)" class="icon" :class="getFavoriteIcon(currentSong)"></i>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <transition name="mini">
      <div class="mini-player" v-show="!fullScreen" @click="open">
        <div class="icon" >
          <img width="40" height="40" :src="currentSong.image" :class="CDClass">
        </div>
        <div class="text">
          <h2 class="name" v-html="currentSong.name"></h2>
          <p class="desc" v-html="currentSong.singer"></p>
        </div>
        <div class="control">
          <progress-circle :percent="percent" :radius="32">
            <i :class="miniPlayIcon " @click.stop="togglePlay" class="icon-mini"></i>
          </progress-circle>
        </div>
        <div class="control" @click.stop="showPlayList">
          <i class="icon-playlist"></i>
        </div>
      </div>
    </transition>
    <play-list ref="playList"></play-list>
   <audio :src="currentSong.url" ref="audio" @play="ready" @error="error" @timeupdate="timeUpdate" @ended="end"></audio>
  </div>
</template>

<script type="text/ecmascript-6">
  import {mapGetters,mapMutations,mapActions} from 'vuex'
  import animations from 'create-keyframe-animation'
  import {prefixStyle} from 'common/js/dom'
  import ProgressBar from 'base/progress-bar/progress-bar'
  import ProgressCircle from 'base/progress-circle/progress-circle'
  import {shuffle} from 'common/js/util'
  import Lyric from 'lyric-parser'
  import Scroll from 'base/scroll/scroll'
  import PlayList from 'components/playlist/playlist'
  import { playMode } from 'common/js/config'
  import {playerMixin} from 'common/js/mixin'

  const transform=prefixStyle('transform')
  const transitionDuration = prefixStyle('transition-duration')
  export default{
      mixins:[playerMixin],
      data(){
          return {
              songReady:false,
              currentTime:0,
              currentLyric:null,
              currentLineNum:0,
              currentShow:'cd',
              playingLyric:''
          }
      },
    components:{
          ProgressBar,
          ProgressCircle,
          Scroll,
         PlayList
    },
    created(){
          this.touch={}
    },
    computed:{
          playIcon(){
             return this.playing ? 'icon-pause':'icon-play'
          },
        miniPlayIcon(){
              return this.playing ? 'icon-pause-mini':'icon-play-mini'
        },
        CDClass(){
            return this.playing ? 'play' :'play pause'
        },
        disableCls(){
          return  this.songReady ? '' : 'disable'
        },
        percent(){
            return  this.currentTime / this.currentSong.duration
        },
        ...mapGetters([
            'fullScreen',
            'currentIndex',
        ])
      },
    watch:{
          currentSong(newSong,oldSong){
              /*刚加载进来时不存在oldSong*/
              if(!newSong.id)
              {
                  return
              }
            if(newSong.id === oldSong.id)
            {
              return
            }
            if(this.currentLyric)
            {
                this.currentLyric.stop()/*当切换歌曲时同时将歌词对象删除*/
                this.currentTime = 0
                this.playingLyric = ''
                this.currentLineNum = 0
            }
            clearTimeout(this.timer)
            this.timer = setTimeout(() => {
                  console.log("playing")
                this.$refs.audio.play();
                this.getLyric()
              },1000)
          },
          playing(newPLaying){
            this.$nextTick(() => {/*注意dome的更改是在下一个tick时发生变化*/
              const audio=this.$refs.audio
              newPLaying ? audio.play():audio.pause()
            })
          }
    },
    methods:{
      back(){
         this.setFullScreen(false);
      },
      open(){
              this.setFullScreen(true);
      },
      togglePlay(){
        if(!this.songReady)return
        this.setPlayingState(!this.playing)
        if(this.currentLyric)
        {
          this.currentLyric.togglePlay()
        }
      },
      playPreSong(){
          if(!this.songReady)return
        /*当歌曲列表中只有一首歌曲的时候直接循环播放*/
          if(this.playList === 1)
          {
            this.loop()
            return
          }
          let currentIndex=this.currentIndex-1;
          if(currentIndex<0)currentIndex=this.playList.length-1
          this.setCurrentIndex(currentIndex)
          if(!this.playing)/*切换歌曲时是歌曲播放，要将playing的状态也跟着变化*/
          {
              this.togglePlay()
          }
        this.songReady=false
      },
      playNextSong(){
        if(!this.songReady)
        {
          return
        }
        if(this.playList === 1)
        {
            this.loop()
            return
        }
        let currentIndex=this.currentIndex+1;
        if(currentIndex===this.playList.length)
        {
            currentIndex=0
        }
          this.setCurrentIndex(currentIndex)
        if(!this.playing)/*切换歌曲时是歌曲播放，要将playing的状态也跟着变化*/
        {
          this.togglePlay()
        }
        this.songReady=false
      },
      end(){
          if(this.mode === playMode.loop)
          {
              this.loop()
          }
          else{
              this.playNextSong()
          }
      },
      loop(){
          this.$refs.audio.currentTime=0
          this.$refs.audio.play()
        if(this.currentLyric)
        {
          this.currentLyric.seek(0)
        }
      },
      ready(){
          /*设置标志位，保证在歌曲加载完毕后在切换歌曲*/
        this.songReady=true
        this.savePlayHistory(this.currentSong)
      },
      error(){
          /*当歌曲加载失败的时候也要保证按钮可以点击下一首*/
        this.songReady=true
      },
      timeUpdate(e){
          this.currentTime=e.target.currentTime
      },
      barTouch(percent){
          let time=percent * this.currentSong.duration
          this.$refs.audio.currentTime=time
          if(!this.playing)
          {
             this.togglePlay()
          }
          /*拖动进度条时同时改变歌词的位置*/
          if(this.currentLyric)
          {
              this.currentLyric.seek(time * 1000)
          }
      },
      format(interval){
          interval=interval | 0
        const minute=this._pad(interval / 60 | 0)
        const second=this._pad(interval % 60 | 0)
        return `${minute}:${second}`
      },
      getLyric(){
          this.currentSong.getLyric().then((lyric) => {
              if(this.currentSong.lyric!==lyric)
              {
                  return
              }
             this.currentLyric=new Lyric(lyric,this.handleLyric)
             if(this.playing)
             {
                 this.currentLyric.play()
             }
          }).catch( () => {
              this.currentLyric=null
              this.playingLyric = ''
              this.currentLineNum = 0
          })
      },
      handleLyric({lineNum,txt}){
         this.currentLineNum = lineNum
        if(lineNum > 5)
        {
            let lineEl = this.$refs.lyricLine[lineNum-5]
            this.$refs.lyricList.scrollToElement(lineEl,1000)
        }
        else   this.$refs.lyricList.scrollTo(0,0,1000)
         this.playingLyric=txt
      },
      middleTouchStart(e){
           this.touch.initialed=true
           const touch=e.touches[0]
           this.touch.startY=touch.pageY
           this.touch.startX=touch.pageX
      },
      middleTouchMove(e){
         if(!this.touch.initialed)
         {
             return
         }
         const touch=e.touches[0]
         const detaY=touch.pageY-this.touch.startY
         const detaX=touch.pageX-this.touch.startX
         if(Math.abs(detaY) > Math.abs(detaX))
         {
             return
         }
         const left= this.currentShow === 'cd' ? 0 : -window.innerWidth
         const offsetWidth = Math.min(Math.max(-window.innerWidth,left + detaX),0)
         this.touch.percent = Math.abs( offsetWidth / window.innerWidth)
         this.$refs.lyricList.$el.style[transform] =`translate3d(${offsetWidth}px,0,0)`
         this.$refs.lyricList.$el.style[transitionDuration] = 0

        this.$refs.middleL.style.opacity=1-this.touch.percent
        this.$refs.middleL.style[transitionDuration] = 0
      },
      middleTouchEnd(e){
         let offsetWidth = 0,opacity
        if(this.currentShow === 'cd')
        {
          if(this.touch.percent > 0.1)
          {
            offsetWidth=-window.innerWidth
            this.currentShow = 'lyric'
            opacity=0
          }
           else{
              offsetWidth = 0
              this.currentShow = 'cd'
              opacity=1
          }
        }
        else{
           if(this.touch.percent < 0.9)
           {
             offsetWidth = 0
             this.currentShow='cd'
             opacity=1
           }
           else {
             offsetWidth = -window.innerWidth
             this.currentShow='lyric'
             opacity=0
           }
        }
        const time = 300
        this.$refs.lyricList.$el.style[transform] =`translate3d(${offsetWidth}px,0,0)`
        this.$refs.lyricList.$el.style[transitionDuration] = `${time} ms`
        this.$refs.middleL.style.opacity=opacity
        this.$refs.middleL.style[transitionDuration] = `${time} ms`

      },
      _pad(num,n=2){
          let len=num.toString().length;
          while(len<n)
          {
             num='0'+num
             len++
          }
          return num
      },
     /*显示歌曲列表*/
      showPlayList(){
          this.$refs.playList.show()
      },
      ...mapMutations({
        setFullScreen:'SET_FULL_SCREEN',
      }),
      ...mapActions(['savePlayHistory']),
      /*动画的钩子函数,下面用的是JS实现的animation*/
      enter(el,done){
         const {x, y,scale}=this._getPosAndScale()
        let animation={
             0:{
               transform:`translate3d(${x}px,${y}px,0) scale(${scale})`
             },
            60:{
              transform:`translate3d(0,0,0) scale(1.1)`
            },
           100:{
             transform:`translate3d(0,0,0) scale(1)`
           }
        }
        animations.registerAnimation({
            name:'move',
            animation,
            presets:{
                duration:400,
                easing:'linear'
            }
        })
        animations.runAnimation(this.$refs.cdWrapper,'move',done)
      },
      afterEnter(el){
         animations.unregisterAnimation('move')
        this.$refs.cdWrapper.style.animation=''
      },
      leave(el,done){
         this.$refs.cdWrapper.style.transition='all 0.4s'
         const {x,y,scale}=this._getPosAndScale();
         this.$refs.cdWrapper.style[transform]=`translate3d(${x}px,${y}px,0) scale(${scale})`
         this.$refs.cdWrapper.addEventListener('transitionend',done)
      },
      afterLeave(el){
          /*和利用样式实现动画类似在动画结束后要将对应的属性清除*/
         this.$refs.cdWrapper.style.transition=''
         this.$refs.cdWrapper.style[transform]=''

      },
      _getPosAndScale(){
          /*获取两个图片的相对位置的差值，以及图形的缩放比*/
          const targetWidth=40
          const paddingLeft=40
          const paddingBottom=30
          const paddingTop=80
          const width=window.innerWidth*0.8
          const scale=targetWidth/width
          const x=-(window.innerWidth/2-paddingLeft)/*从大图到小图的中心点*/
          const y=window.innerHeight-paddingTop-width/2-paddingBottom
          return {x,y,scale}
      },
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"

  .player
    .normal-player
      position: fixed
      left: 0
      right: 0
      top: 0
      bottom: 0
      z-index: 150
      background: $color-background
      .background
        position: absolute
        left: 0
        top: 0
        width: 100%
        height: 100%
        z-index: -1
        opacity: 0.6
        filter: blur(20px)
      .top
        position: relative
        margin-bottom: 25px
        .back
          position absolute
          top: 0
          left: 6px
          z-index: 50
          .icon-back
            display: block
            padding: 9px
            font-size: $font-size-large-x
            color: $color-theme
            transform: rotate(-90deg)
        .title
          width: 70%
          margin: 0 auto
          line-height: 40px
          text-align: center
          no-wrap()
          font-size: $font-size-large
          color: $color-text
        .subtitle
          line-height: 20px
          text-align: center
          font-size: $font-size-medium
          color: $color-text
      .middle
        position: fixed
        width: 100%
        top: 80px
        bottom: 170px
        white-space: nowrap
        font-size: 0
        .middle-l
          display: inline-block
          vertical-align: top
          position: relative
          width: 100%
          height: 0
          padding-top: 80%
          .cd-wrapper
            position: absolute
            left: 10%
            top: 0
            width: 80%
            height: 100%
            .cd
              width: 100%
              height: 100%
              box-sizing: border-box
              border: 10px solid rgba(255, 255, 255, 0.1)
              border-radius: 50%
              &.play
                animation: rotate 20s linear infinite
              &.pause
                animation-play-state: paused
              .image
                position: absolute
                left: 0
                top: 0
                width: 100%
                height: 100%
                border-radius: 50%

          .playing-lyric-wrapper
            width: 80%
            margin: 30px auto 0 auto
            overflow: hidden
            text-align: center
            .playing-lyric
              height: 20px
              line-height: 20px
              font-size: $font-size-medium
              color: $color-text-l
        .middle-r
          display: inline-block
          vertical-align: top
          width: 100%
          height: 100%
          overflow: hidden
          .lyric-wrapper
            width: 80%
            margin: 0 auto
            overflow: hidden
            text-align: center
            .text
              line-height: 32px
              color: $color-text-l
              font-size: $font-size-medium
              &.current
                color: $color-text
      .bottom
        position: absolute
        bottom: 50px
        width: 100%
        .dot-wrapper
          text-align: center
          font-size: 0
          .dot
            display: inline-block
            vertical-align: middle
            margin: 0 4px
            width: 8px
            height: 8px
            border-radius: 50%
            background: $color-text-l
            &.active
              width: 8px;
              border-radius: 50%;
              background: $color-text-ll
        .progress-wrapper
          display: flex
          align-items: center
          width: 80%
          margin: 0px auto
          padding: 10px 0
          .time
            color: $color-text
            font-size: $font-size-small
            flex: 0 0 30px
            line-height: 30px
            width: 30px
            &.time-l
              text-align: left
            &.time-r
              text-align: right
          .progress-bar-wrapper
            flex: 1
            margin:0 8px 0 5px
        .operators
          display: flex
          align-items: center
          .icon
            flex: 1
            color: $color-theme
            &.disable
              color: $color-theme-d
            i
              font-size: 30px
          .i-left
            text-align: right
          .i-center
            padding: 0 20px
            text-align: center
            i
              font-size: 40px
          .i-right
            text-align: left
          .icon-favorite
            color: $color-sub-theme
      &.normal-enter-active, &.normal-leave-active
        transition: all 0.4s
        .top, .bottom
          transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32)
      &.normal-enter, &.normal-leave-to
        opacity: 0
        .top
          transform: translate3d(0, -100px, 0)
        .bottom
          transform: translate3d(0, 100px, 0)
    .mini-player
      display: flex
      align-items: center
      position: fixed
      left: 0
      bottom: 0
      z-index: 180
      width: 100%
      height: 60px
      background: $color-highlight-background
      &.mini-enter-active, &.mini-leave-active
        transition: all 0.4s
      &.mini-enter, &.mini-leave-to
        opacity: 0
      .icon
        flex: 0 0 40px
        width: 40px
        padding: 0 10px 0 20px
        img
          border-radius: 50%
          &.play
            animation: rotate 10s linear infinite
          &.pause
            animation-play-state: paused
      .text
        display: flex
        flex-direction: column
        justify-content: center
        flex: 1
        line-height: 20px
        overflow: hidden
        .name
          margin-bottom: 2px
          no-wrap()
          font-size: $font-size-medium
          color: $color-text
        .desc
          no-wrap()
          font-size: $font-size-small
          color: $color-text-d
      .control
        flex: 0 0 30px
        width: 30px
        padding: 0 10px
        .icon-play-mini, .icon-pause-mini, .icon-playlist
          font-size: 30px
          color: $color-theme-d
        .icon-mini
          font-size: 32px
          position: absolute
          left: 0
          top: 0

  @keyframes rotate
    0%
      transform: rotate(0)
    100%
      transform: rotate(360deg)
</style>



